* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-family: sans-serif;
}
.heading {
  background-color: #f5ba13;
}
body {
  background-color: #eee;
}
.heading > h1 {
  padding: 2%;
}
.note {
  height: 30%;
  width: 40%;
  padding: 1%;
  font-size: 0.8rem;
  margin-left: 2%;
  float: left;

  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}
.foot {
  margin-bottom: 0;
  text-align: center;
  width: 100%;
  height: 2.5rem;
  position: absolute;
  top: 85%;
}
.foot > p {
  color: #ccc;
}
